.background-image {
  background-repeat: no-repeat;
  object-fit: contain;
  max-width: 100%;
  height: 80vh;
  background-size: cover;
  background-position: top;
}

.gradient {
  background-image: linear-gradient(0deg, rgb(20, 20, 20) 4%, rgba(20, 20, 20, 0.46) 100%);
  position: relative;
  height: 100%;
}

.content-wrapper	{
  z-index: 10;
  width: 100%;
  height: 100%;
}

.content {
  padding: 14em 50px;
  height: 10em;
}

.title {
  font-size: 4.5rem;
  line-height: 1.25em;
  margin: 0;
}

.overview {
  max-width: 900px;
  display: flex;
  flex-wrap: wrap;
}

.btn {
  cursor: pointer;
  font-size: 1rem;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
  background-color: rgba(51,51,51,.7);
  margin-bottom: 0.85em;
  width: 90px;
}

.link {
  color: #e5e5e5;
  text-decoration: none;
}
